@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
*, :before, :after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
}

html {
  box-sizing: border-box;
}

body {
  color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.body_overflow-hidden {
  overflow: hidden;
}

.container {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  display: block;
}

.page_new-year {
  min-height: 100%;
  background: url("new-year.8a8bf4c1.jpeg") center / cover no-repeat;
}

.intro {
  padding: 40px 0;
}

.intro__wrapper {
  flex-direction: column;
  gap: 80px;
  display: flex;
}

.calendar {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 1em;
  font-size: 30px;
  display: grid;
}

.calendar__day {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: #0006;
  border: .125em dashed #646464;
  padding: .7em;
  transition-property: background-color, transform;
  transition-duration: .2s;
  transition-timing-function: ease-in-out;
}

.calendar__day:hover:not(.calendar__day_active) {
  background-color: #000c;
  transform: scale(1.1);
}

.calendar__day_active {
  cursor: auto;
  -webkit-user-select: auto;
  user-select: auto;
}

.calendar__day_active .calendar__day-preview {
  display: none;
}

.calendar__day_active .calendar__popup {
  display: block;
}

.calendar__popup {
  display: none;
}

@media (max-width: 600px) {
  .calendar {
    grid-template-columns: 1fr 1fr;
  }
}

.popup {
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  background-color: #000c;
  transition-property: opacity, visibility;
  transition-duration: .4s;
  transition-timing-function: ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.popup_open {
  visibility: visible;
  opacity: 1;
}

.popup__close {
  cursor: pointer;
  font-size: 1.5em;
  position: absolute;
  top: 3vmin;
  right: 3vmin;
}

.popup__body {
  min-height: 100%;
  justify-content: center;
  align-items: center;
  padding: 100px 30px;
  display: flex;
}

.popup__content {
  max-width: 800px;
  width: 100%;
  color: #000;
  background-color: #fff;
  flex-direction: column;
  gap: 1.5em;
  padding: 2em;
  display: flex;
}

.popup__title, .popup__description {
  margin: 0;
}

